.toasts-container {
    position: absolute;
    pointer-events: none;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 3;
    overflow: hidden;
    display: grid;
    grid-template-columns: 1fr 1fr;

    .left,
    .right {
        display: flex;
        flex-flow: column-reverse nowrap;
    }
}

.toast {
    pointer-events: all;
    flex-shrink: 0;
    max-width: 350px;
    border-radius: 5px;
    margin-top: 8px;
    border: 4px ridge white;
    background-color: black;
    animation: toast .2s cubic-bezier(.17, .67, .74, 1.53);
    color: white;
    padding: 10px;
    background-image: url(data:image/png;base64,);
    position: relative;
    transition: all .1s ease-out; // closing speed

    &.close {
        z-index: -1;
        transform: translateY(100px);
    }

    svg {
        position: absolute;
        top: 4px;
        right: 4px;
        height: 20px;
        width: 20px;
        stroke-width: 3px;
        cursor: pointer;
    }

    &.toast-achievement {
        box-shadow: 0 0 10px #ffd700a6;
    }

    .toast-top {
        font-family: "Asap Condensed";
        font-weight: bold;
        font-size: 20px;
        display: grid;
        grid-template-columns: 32px auto;
        column-gap: 10px;
        align-items: center;
    }

    .toast-middle {
        padding: 10px 0;
    }
}