.tab-content-store {
    display: flex;
    flex-flow: column nowrap;

    .upgrades-container {
        box-sizing: content-box;
        display: grid;
        grid-template-columns: 60px 60px 60px 60px 60px;
        grid-auto-rows: 60px;
        height: 60px;
        overflow: hidden;
        border-top: 10px solid saddlebrown;
        border-image: url('./../../static/images/misc-horizontal_separator.png') 10 round;
        transition: height .1s ease-out;
        background-color: #222;
        background-image: url(data:image/png;base64,);

        .upgrade {
            background-image: url('./../../static/images/upgrade-upgradeContainer.png');
            background-size: 100% 100%;
            cursor: pointer;
            background-color: #222;
            display: flex;
            justify-content: center;
            align-items: center;

            &:hover {
                box-shadow: 0 0 5px white inset;
            }

            &.not-enough {
                filter: grayscale(100%);
                opacity: .5;
            }

            * {
                pointer-events: none;
            }
        }
    }

    .buy-amount-container {
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-between;
        font-family: 'Germania One';
        padding: 4px 8px;
        color: white;
        background-color: black;
        background-image: url('./../../static/images/misc-horizontal_separator.png');
        background-size: auto 100%;
        image-rendering: pixelated;
        font-size: 14px;

        .buy-amount-text {
            letter-spacing: .6px;
        }

        .buy-amounts {
            display: grid;
            grid-template-columns: min-content min-content min-content min-content;
            column-gap: 10px;

            .buy-amount {
                color: darken(white, 40%);
                cursor: pointer;

                &.selected {
                    color: white;
                }
            }
        }
    }

    .buildings-container {
        flex: 1 1 auto;
        background-color: #614938;
        box-shadow: 0 0 5px #222 inset;
        overflow-y: auto;
        height: 300px;

        .building {
            background-image: url('./../../static/images/misc-building_button.png');
            background-size: 100% 100%;
            padding-right: 16px;
            height: 74px; // 64 + 10px from padding
            font-family: 'Germania One';
            color: white;
            display: grid;
            grid-template-columns: 64px auto min-content;
            column-gap: 10px;
            align-items: center;
            text-shadow: 0 0 1px black;
            letter-spacing: .5px;
            cursor: pointer;

            .building-img {
                filter: drop-shadow(3px 3px 1px black);
            }

            .building-left {

                .building-name {
                    font-size: 24px;
                }

                .building-price {
                    display: flex;
                    flex-flow: row nowrap;
                    align-items: center;
                    color: black;
                    text-shadow: none;

                    &.not-enough {
                        color: crimson;
                    }

                    img {
                        height: 16px;
                        margin-right: 4px;
                    }
                }
            }

            .building-owned {
                font-size: 48px;
                letter-spacing: 0;
            }

            &.locked {
                cursor: not-allowed;
                opacity: .4;

                img {
                    filter: brightness(0%);
                }
            }

            &:hover:not(.locked) {
                box-shadow: 0 0 5px #fff inset;
            }
        }
    }
}