.tooltip-wrapper {
    position: absolute;
    display: none;
    background-color: black;
    background-image: url(data:image/png;base64,);
    color: white;
    z-index: 3;
    pointer-events: none;
    border: 5px ridge #353535;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.6);
    padding: 8px;

    &.visible {
        display: block;
    }

    .tooltip-container {

        .tooltip-top {
            font-family: 'Germania One';
            display: grid;
            grid-template-columns: 50px auto max-content;
            column-gap: 8px;
            align-items: center;
            font-size: 18px;
            border-bottom: 1px solid rgba(255, 255, 255, .4);

            >img {
                height: 50px;
            }

            .tooltip-target-name-container {
                display: flex;
                flex-flow: column nowrap;

                p {
                    text-shadow: 0 0 2px black;
                    letter-spacing: .2px;
                }

                small {
                    font-size: 12px;
                    font-family: 'Asap Condensed';
                    letter-spacing: .1px;
                    color: rgba(255, 255, 255, .6);
                }
            }

            .price {
                font-size: 16px;
                font-family: 'Asap Condensed';
                display: flex;
                flex-flow: row nowrap;
                align-items: center;

                img {
                    height: 14px;
                    margin-right: 4px;
                    margin-top: 4px;
                }
            }
        }

        .tooltip-middle {
            padding: 8px 0;

            .cost {
                padding-top: 8px;
            }

            .stats {
                padding-top: 8px;
                font-size: 14px;
                opacity: .8;
            }
        }

        .tooltip-bottom {
            font-size: 12px;
            opacity: .6;
        }
    }
}